import axios from 'axios'

export default {
  name: 'runMission',
  data () {
    return {
      missionList: Array
    }
  },
  methods: {
    getMissionList () {
      axios.get(this.url + '/mission/participant/' + localStorage.getItem('id'))
        .then(this.getMissionListSucc)
    },
    getMissionListSucc (res) {
      res = res.data
      if (res === 'MissionFindByParticipants No Project Record') this.$router.push('/noProject')
      else if (res) {
        this.missionList = res
      }
    }
  },
  mounted () {
    this.getMissionList()
  }
}



