import RunMission from './runMission'
import FinishedMission from './finishedMission'

export default {
  name: 'missionHeader',
  components: {
    RunMission,
    FinishedMission
  },
  data () {
    return {
      activeIndex: '1',
      Run: '',
      Finished: ''
    }
  },
  mounted: function () {
    this.Run = true
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
    },
    showRun () {
      this.Run = true
      this.Finished = false
    },
    showFinished () {
      this.Finished = false
      this.Finished = true
      this.Run = false
    }
  }
}



