import MissionHeader from './components/header'
export default {
  name: 'index',
  components: {
    MissionHeader
  },
  methods: {
    judge () {
      if (!localStorage.getItem('id')) {
        this.$router.push('/')
        alert('您还没有绑定用户哦！')
      }
    }
  },
  created () {
    this.judge()
  }
}



