import axios from 'axios'

export default {
  name: 'runMission',
  data () {
    return {
      missionList: []
    }
  },
  methods: {
    getMissionList () {
      axios.get(this.url + '/mission/all')
        .then(this.getMissionListSucc)
    },
    getMissionListSucc (res) {
      if (res.status !== 200) this.$router.push('/noProject')
      else if (res) {
        res = res.data
        this.missionList = res
        console.log(this.missionList)
      }
    },
    jump () {
      alert('hhhhh')
    }
  },
  mounted () {
    this.getMissionList()
  }
}



